"use strict";

/**
 * ,      
 * @module EventBus
 */
export default class EventBus {
	constructor() {
		this.listeners = {};
	}

	/**
	 *   
	 * @param {string} event -  
	 * @param {function} listener -     
	 */
	on(event, listener) {
		this.listeners[event] = this.listeners[event] || [];
		this.listeners[event].push(listener);
	}


	/**
	 *  
	 * @param {string} event -  
	 * @param {object} data -  ,     
	 */
	emit(event, data) {
		this.listeners[event].forEach((listener) => {
			listener(data);
		})
	}
}




