'use strict';

import Http from "../modules/http";

/**
 *     
 * @module UserService
 */
export default class UserService {
	constructor() {
		this.user = null;
		this.users = [];
	}


	/**
	 *   
	 * @param {string} username
	 * @param {string} email
	 * @param {string} password
	 * @param {string} confirm
	 */
	async signup(username, email, password, confirm) {

		const response = {
			ok: false,
			json: {},
			message: "",
		};

		if (username.length < 4) {
			response.message = "      4 !";
			return response;
		}
		if (username.length > 12) {
			response.message = "     12 !";
			return response;
		}
		if (password.length < 6) {
			response.message = "      6 !";
			return response;
		}
		if (password !== confirm) {
			response.message = "  !!!";
			return response;
		}
		if (password === username) {
			response.message = "     !";
			return response;
		}

		const resp = await Http.FetchPost('/user/sign_up', {username, email, password});
		response.json = await resp.json();
		if (resp.status >= 400) {
			response.message = response.json.errorMessage;
			return response;
		}

		response.ok = true;
		this.user = response.json;
		return response;
	}


	/**
	 *  
	 * @param {string} login
	 * @param {string} password
	 */
	async login(login, password) {

		const response = {
			ok: false,
			json: {},
			message: "",
		};

		if (password.length < 6) {
			response.message = "  !  - ";
			return response;
		}
		if (password === login) {
			response.message = "     !";
			return response;
		}

		const resp = await Http.FetchPost('/user/sign_in', {login, password});
		response.json = await resp.json();
		if (resp.status >= 400) {
			response.message = response.json.errorMessage;
			return response;
		}

		response.ok = true;
		this.user = response.json;
		return response;
	}


	/**
	 *    
	 * @param {string} username
	 * @param {string} email
	 * @param {string} password
	 * @param {string} old_password
	 */
	async update(username, email, password, old_password) {//  JSON
		const response = {
			ok: false,
			json: {},
			message: "",
		};

		if (username.length < 4) {
			response.message = "      4 !";
			return response;
		}
		if (username.length > 12) {
			response.message = "     12 !";
			return response;
		}
		if (password.length < 6) {
			response.message = "      6 !";
			return response;
		}
		if (old_password.length < 6) {
			response.message = "      6 !";
			return response;
		}
		if (password === username) {
			response.message = "     !";
			return response;
		}
		if (old_password === username) {
			response.message = "     !";
			return response;
		}

		const resp = await Http.FetchPost('/user/update', {login, password});
		response.json = await resp.json();

		if (resp.status >= 400) {
			response.message = response.json.errorMessage;
			return response;
		}

		response.ok = true;
		this.user = response.json;
		return response;
	}


	/**
	 * ,   
	 * @return {boolean}
	 */
	isLoggedIn() {
		return !!this.user;
	}


	/**
	 * ,   
	 * @param force -        true
	 * @return {Promise} -        
	 */
	async getDataFetch(force = false) {//   ?
		const response = {
			ok: false,
			json: {},
			message: "",
		};

		if (this.isLoggedIn() && !force) {
			response.ok = true;
			response.json = this.user;
			return response;
		}

		const resp = await Http.FetchGet('/user/whoisit');
		response.json = await resp.json();

		if (resp.status >= 400) {
			this.user = null;
			this.users = [];
			response.message = response.json.errorMessage;
			return response;
		}

		response.ok = true;
		this.user = response.json;
		return response;
	}


	/**
	 * 
	 */
	logout() {
		if (this.isLoggedIn()) {
			this.user = null;
			this.users = [];
			Http.FetchGet('/user/exit');
		}
	}


	/**
	 *   
	 */
	loadUsersList() {
		return Http.FetchGet('')
	}
}



